import myAxios from './axios';

// 上传图片
export function uploadImageAPI(paramsList) {
  return myAxios({
    url: '/uploadimg',
    method: 'post',
    data: paramsList,
  })
}

// 删除图片
export function deleteImageAPI(paramsList) {
  return myAxios({
    url: '/deleteimg',
    method: 'post',
    data: paramsList,
    async: false
  })
}
