import myAxios from './axios';

// 用户登录
export function userLoginAPI(paramsList) {
  return myAxios({
    url: '/user/login',
    method: 'get',
    params: paramsList
  })
}

// 用户注册
export function userRegisterAPI(paramsList) {
  return myAxios({
    url: '/user/register',
    method: 'post',
    data: paramsList
  })
}

// 获取用户信息
export function getUserinfoAPI(paramsList) {
  return myAxios({
    url: '/user/userinfo',
    method: 'get',
    params: paramsList
  })
}

// 更新用户个人资料
export function editUserInfoAPI(paramsList) {
  return myAxios({
    url: '/user/edituserinfo',
    method: 'post',
    data: paramsList
  })
}